import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { Subscription } from 'rxjs';
import { SuppliersService } from "src/app/erp-suppliersmanagement/data/supplier-maintenance.service";


@Component({
  selector: "app-gen-widgets",
  templateUrl: "./gen-widgets.component.html",
  styleUrls: ["./gen-widgets.component.scss"],
})
export class GenWidgetsComponent implements OnInit {
  suppliersPendingCount: number = 0;
  approvedSuppliersCount: number = 0;
  verifiedSuppliersCount: number = 0;
  rejectedSuppliersCount: number = 0;

  private subscriptions: Subscription[] = [];

  constructor(
    private router: Router,
    private suppliersService: SuppliersService
  ) {}

  ngOnInit(): void {
    this.getSuppliersPendingCount();
    this.getApprovedSuppliersCount();
    this.getVerifiedSuppliersCount();
    this.getRejectedSuppliersCount();
  }

  getSuppliersPendingCount(): void {
    let params = {
      status: "PENDING",
    };
    this.subscriptions.push(
      this.suppliersService.getSuppliersByStatus(params).subscribe((res) => {
        this.suppliersPendingCount = res.entity.length;
      })
    );
  }

  getApprovedSuppliersCount(): void {
    let params = {
      status: "APPROVED",
    };
    this.subscriptions.push(
      this.suppliersService.getSuppliersByStatus(params).subscribe((res) => {
        this.approvedSuppliersCount = res.entity.length;
      })
    );
  }

  getVerifiedSuppliersCount(): void {
    let params = {
      status: "VERIFIED",
    };
    this.subscriptions.push(
      this.suppliersService.getSuppliersByStatus(params).subscribe((res) => {
        this.verifiedSuppliersCount = res.entity.length;
      })
    );
  }

  getRejectedSuppliersCount(): void {
    let params = {
      status: "REJECTED",
    };
    this.subscriptions.push(
      this.suppliersService.getSuppliersByStatus(params).subscribe((res) => {
        this.rejectedSuppliersCount = res.entity.length;
      })
    );
  }

  ngOnDestroy(): void {
    // Unsubscribe from all subscriptions to avoid memory leaks
    this.subscriptions.forEach(subscription => subscription.unsubscribe());
  }

  navigateToPendingSuppliers(): void {
    this.router.navigate(["/erp-suppliers-management/suppliers-maintenance/all-suppliers"]);
  }

  navigateToApprovedSuppliers(): void {
    this.router.navigate(["/erp-suppliers-management/suppliers-maintenance/all-suppliers"]);
  }

  navigateToVerifiedSuppliers(): void {
    this.router.navigate(["/erp-suppliers-management/suppliers-maintenance/all-suppliers"]);
  }

  navigateToRejectedSuppliers(): void {
    this.router.navigate(["/erp-suppliers-management/suppliers-maintenance/all-suppliers"]);
  }
}